<template>
  <div id="app">
    <!--  -->
    <nav class="navbar">
      <!--  logo  -->
      <img src="./assets/img/logo.png" alt="Logo" class="logo" />
      <ul>
        <li
          v-for="(page, index) in pages"
          :key="index"
          :class="{ active: currentPage === index }"
          @click="goToSection(index)"
        >
          {{ page }}
        </li>
      </ul>
    </nav>

    <div id="fullpage">
      <div
        v-for="(component, index) in pageComponents"
        :key="index"
        class="section"
        :class="'section' + (index + 1)"
      >
        <component :is="component" />
      </div>
    </div>
  </div>
</template>

<script>
import fullpage from 'fullpage.js';
import 'fullpage.js/dist/fullpage.css';

// 
import PageOne from './components/PageOne.vue';
import PageTwo from './components/PageTwo.vue';
import PageThree from './components/PageThree.vue';
import PageFour from './components/PageFour.vue';
import PageFive from './components/PageFive.vue';

export default {
  name: 'App',
  components: {
    PageOne,
    PageTwo,
    PageThree,
    PageFour,
  },
  data() {
    return {
      pages: ['', '', '', '',''], // 
      currentPage: 0, // 
      pageComponents: [PageOne, PageTwo, PageThree, PageFour, PageFive], // 
      fullpageInstance: null, //  fullpage.js 
    };
  },
  mounted() {
    //  fullpage.js
    this.fullpageInstance = new fullpage('#fullpage', {
      navigation: true,
      scrollingSpeed: 700,
      anchors: this.pages.map((_, index) => `page${index + 1}`), // 
      navigationTooltips: this.pages, //  pages 
      showActiveTooltip: true,
      afterLoad: (origin, destination) => {
        this.currentPage = destination.index; // 
      },
    });
  },
  methods: {
    // 
    goToSection(index) {
      if (this.fullpageInstance) {
        this.fullpageInstance.moveTo(index + 1); //  API
      }
    },
  },
};
</script>

<style>
/*  */
#app {
  font-family: Arial, sans-serif;
}

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #333;
  color: #fff;
  display: flex;
  justify-content: space-between; /*  logo  */
  align-items: center;
  padding: 10px 20px;
  z-index: 1000;
}

.navbar .logo {
  height: 40px; /*  logo  */
}

.navbar ul {
  list-style-type: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  flex-grow: 1; /*  */
  justify-content: center; /*  */
}

.navbar li {
  cursor: pointer;
  padding: 5px 10px;
  font-size: 18px;
}

.navbar li.active {
  font-weight: bold;
  color: #ffd700;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #e4e0cf; /*  */
   /*  */
  
  background-repeat: no-repeat; /*  */
}

.section1 {
  background-image: url('./assets/img/background.png'); /*  */
  background-position: left bottom; /*  */
  background-size: 50%;
}

.section2 {
  background-image: url('./assets/img/background3.png'); /*  */
  background-position: right bottom;
  background-size: 35%;
}

.section3 {
  background-image: url('./assets/img/background3.png'); /*  */
  background-position: right bottom;
  background-size: 35%;
}

.section4 {
  background-image: url('./assets/img/background.png'); /*  */
  background-position: left bottom; /*  */
  background-size: 50%;
}

.section5 {
  background-image: url('./assets/img/background.png'); /*  */
  background-position: left bottom; /*  */
  background-size: 50%;
}
</style>
