<template>
  <div class="page-container">
    <!--  -->
    <div class="chapter-list">
      <div
        v-for="(chapter, index) in chapters"
        :key="index"
        class="chapter-item"
        :class="{ active: activeChapter === index }"
        @click="selectChapter(index)"
      >
        {{ chapter }}
      </div>
    </div>
    <!--  -->
    <div class="page-content">
      <!--  -->
      <div ref="mapContainer" class="map-container">
        <img src="/xuxiake/images/south.png" alt="" class="south-china-sea-image" />
      </div>
    </div>
    <!--  -->   
    <div class="story-content">
      <div id="place-impo-container" class="top-content">
        <!--  -->
        <h3 class="title"></h3>
        <p class="inner-content" v-html="placeInfoDisplay"></p>
      </div>
      <div id="place-info-container" class="bottom-content">
        <!--  -->
        <h3 class="title"></h3>
        <p class="inner-content" v-html="placeImpoDisplay"></p>
      </div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "PageOne",
  data() {
    return {
      timelineY: 0,
      timelineX: 0,
      len: 4,
      init:0,

      svg: null,        // SVG 
      projection: null, // 

      chapters: [], // 
      activeChapter: null, // 

      locations: [], // 
      selectedLocations: [], // 

      timeData: [], // 
      selectedTimeData: [], // 

      lines: [],  // 

      placeInfoDisplay: "", // 
      placeImpoDisplay: "", // .", // 
    };
  },
  async mounted() {
    // 
    await this.initOnResize();
    // 
    window.addEventListener('resize', this.initOnResize);
  },
  beforeUnmount() {
    // 
    window.removeEventListener('resize', this.initOnResize);
  },
  methods: {

    async initOnResize() {
      this.resetData();
      // 
      await Promise.all([
        this.loadChapters(),  // 
        this.loadLocationData(),  // 
        this.loadTimeData(),  // 
      ]);
      this.initMap();  // 
    },
    resetData() {
      this.timelineY = 0;
      this.timelineX = 0;
      this.init = 0;
      const container = this.$refs.mapContainer; // 
      // // 
      // const images = container.querySelectorAll('img');
      // images.forEach(img => img.remove());  // 

      //  SVG 
      const svgs = container.querySelectorAll('svg');
      svgs.forEach(svg => svg.remove());  //  SVG 
      this.svg = null;        // SVG 
      this.projection = null; // 
    },
       // 
    async loadChapters() {
      try {
        const response = await fetch("/xuxiake/data/chapters.json"); // JSON 
        const data = await response.json();
        this.chapters = data.chapters;
      } catch (error) {
        console.error("Failed to load chapters:", error);
      }
    },
    // 
    extractDate(timeString) {
      if (timeString == null) {
        return new Date(1940, 0, 1); //  1940  1  1 
      }

      // 1. 
      const yearRegex = /[(](\d{4})[)]/;
      const yearMatch = timeString.match(yearRegex); // 
      const year = yearMatch ? parseInt(yearMatch[1], 10) : 1940;  //  1940 

      // 2. 
      const monthRegex = /(\d+)(?=)/;  // ""
      const monthMatch = timeString.match(monthRegex);
      const month = monthMatch ? parseInt(monthMatch[1], 10) - 1 : 0;  // 01

      // 3. 
      const dayRegex = /(\d+)(?=)/;  // ""
      const dayMatch = timeString.match(dayRegex);
      const day = dayMatch ? parseInt(dayMatch[1], 10) : 1;  // 1

      // 
      return new Date(year, month, day);  // 
    },

    // 
    async loadLocationData() {
      try {
        const response = await fetch("/xuxiake/data/dataset_total.json"); // 
        const data = await response.json();
        // 
        this.locations = data.map(item => ({
          name: item[""],
          lon: item[""],
          lat: item[""],
          chapter: item[""],  // 
          route: item[""],    // 
          // time: this.extractYear(item[""])  // 
        }));
        
      } catch (error) {
        console.error("Failed to load location data:", error);
      }
    },
    // 
    async loadTimeData() {
      try {
        const response = await fetch("/xuxiake/data/dataset_time.json"); // 
        const data = await response.json();
        // 
        this.timeData = data.map(item => ({
          name: item[""],
          lon: item[""],
          lat: item[""],
          chapter: item[""],  // 
          route: item[""],    // 
          time: this.extractDate(item[""]),  // 
          info: item[""],
          impo: item[""],
        }));
        console.log("Original data length:", data.length);
        console.log("Processed timeData length:", this.timeData.length);

      } catch (error) {
        console.error("Failed to load location data:", error);
      }
    },

    // 
    selectChapter(index) {
      this.activeChapter = index;
      const selectedChapter = this.chapters[index];
      console.log("1");
      // ""
      this.selectedLocations = this.locations.filter(item => item.chapter === selectedChapter);
      // ""
      this.selectedTimeData = this.timeData.filter(item => item.chapter === selectedChapter);
      this.init = 1;
      // 
      this.relate();
    },
    // 
    async drawMapPath(svg, projection) {
      return new Promise((resolve, reject) => {
        d3.json("/xuxiake/data/china.geo.json").then(mapData => {
          const path = d3.geoPath().projection(projection);
          svg.append("g")
            .selectAll("path")
            .data(mapData.features)
            .enter()
            .append("path")
            .attr("d", path)
            .style("fill", "#8bab8d")//v1:f9f9f9
            .on("mouseover", (event, d) => {
              d3.select(event.currentTarget).style("fill", "#597b7a");//(v1:b7ae8f)
              this.createTooltip(svg, d, event);
            })
            .on("mousemove", (event) => {
              this.removeTooltip();
              this.createTooltip(svg, null, event);
            })
            .on("mouseout", (event) => {
              d3.select(event.currentTarget).style("fill", "#8bab8d");//(v1:f9f9f9)
              this.removeTooltip();
            });

          //  resolve
          resolve();
        }).catch(reject); //  reject
      });
    },
    // 
    async relate() {
      // const container = this.$refs.mapContainer;
      // const width = container.clientWidth;
      // const height = container.clientHeight;
      // const centerX = width / 2;
      

      const svg = d3.select(this.$refs.mapContainer).select("svg");  //  svg 
      svg.selectAll("circle").remove();  // 
      svg.selectAll(".location-line").remove();
      svg.selectAll(".tick").remove();
      // 
      await this.drawLocationCircles(svg, this.projection, this.selectedLocations);
      await this.drawLocationLines(svg, this.projection, this.selectedLocations, this.len);
      
      //  g 
      const timelineGroup = svg.select(".timeline-group");
    
      // const timelineWidth = svg.node().getBoundingClientRect().width;
      const timelineWidth = this.timelineX;
      const timelineHeight = 60;  // 

      const centerY = this.timelineY;

      // 1. 
      svg.selectAll(".timeline-link").remove();
      svg.selectAll(".location-circle").remove();  // 
      timelineGroup.selectAll(".timeline-point").remove(); // axis-label
      svg.selectAll(".axis-label").remove();
      timelineGroup.selectAll(".timeline-text").remove();
      // 
      timelineGroup.select(".timeline-axis").remove();

      // 
      const minDate = d3.min(this.selectedTimeData, d => new Date(d.time));
      const maxDate = d3.max(this.selectedTimeData, d => new Date(d.time));

      // 
      const timeSpan = maxDate - minDate;

      // 
      let adjustedMinDate, adjustedMaxDate;
      if (timeSpan <= 365 * 24 * 60 * 60 * 1000) {  // 
        adjustedMinDate = d3.timeMonth.offset(minDate, -1);  // 1
        adjustedMaxDate = d3.timeMonth.offset(maxDate, 1);   // 1
      } else {  // 
        adjustedMinDate = d3.timeYear.offset(minDate, -1);   // 1
        adjustedMaxDate = d3.timeYear.offset(maxDate, 1);    // 1
      }

      // 2. 
      const xScale = d3.scaleTime()
        .domain([adjustedMinDate, adjustedMaxDate])
        .range([30, timelineWidth - 30]);

      // 
      const interval = timeSpan <= 365 * 24 * 60 * 60 * 1000 // 
        ? d3.timeMonth.every(1)  // 
        : d3.timeYear.every(1);  // 

      // 
      const axisBottom = d3.axisBottom(xScale).ticks(interval).tickFormat(d3.timeFormat("%Y-%m"));
      timelineGroup.append("g")
        .attr("class", "timeline-axis")
        .attr("transform", `translate(0, ${centerY + timelineHeight / 2})`)
        .call(axisBottom)
        .selectAll(".tick text") // 
        .style("font-size", "15px") // 
        .style("font-weight", "bold") // 
        .style("fill", "#000000"); //

      // 
      timelineGroup.selectAll(".tick line") // 
        .attr("y1", -30)
        .attr("y2", timelineHeight - 30)
        .style("stroke", "#b7ae8f") // 
        .style("stroke-width", "1"); // 

      //  ( path )
      timelineGroup.select(".timeline-axis path")
          .style("display", "none"); // 

      // 
      timelineGroup.selectAll(".timeline-point")
        .data(this.selectedTimeData)
        .enter()
        .append("circle")
        .attr("class", "timeline-point")
        .attr("cx", (d, i) => {
          const baseX = xScale(new Date(d.time)); // 
          const offsetX = i * 0;  // 
          return baseX + offsetX; // X
        })
        .attr("cy", centerY + timelineHeight / 2) // 
        .attr("r", 6)
        .attr("fill", "#b7ae8f")// 
        .style("opacity", 0.7);   
        
      const mini = 40; // 
      // 2. 
      timelineGroup.selectAll(".timeline-text")
        .data(this.selectedTimeData)
        .enter()
        .append("text")
        .attr("class", "timeline-text")
        .attr("x", (d, i) => {
          const baseX = xScale(new Date(d.time)); // 
          const offsetX = i * 0;  // 
          return baseX + offsetX;
        })
        .attr("y", centerY + 20 + timelineHeight / 2) // 
        .attr("dy", -30) // 
        .attr("text-anchor", "middle") // 
        .text((d, i, nodes) => {
          const currentX = xScale(new Date(d.time)); // x
          const leftX = i > 0 ? xScale(new Date(this.selectedTimeData[i - 1].time)) : -Infinity;
          const rightX = i < this.selectedTimeData.length - 1 ? xScale(new Date(this.selectedTimeData[i + 1].time)) : Infinity;
          // 
          const date = new Date(d.time);
          const options = { month: '2-digit', day: '2-digit' }; //  MM/DD

          if(i===0 || i===this.selectedTimeData.length-1){
              return new Intl.DateTimeFormat('en-US', options).format(date); //  'en-US' 
            }
          //  mini:&& Math.abs(currentX - rightX) < mini
          if (Math.abs(currentX - leftX) < mini) {
            nodes[i].remove(); // 
            return null; // 
          }
          if(i===this.selectedTimeData.length-2 && Math.abs(currentX - rightX) < mini){
            nodes[i].remove(); // 
            return null; // 
          }
          return new Intl.DateTimeFormat('en-US', options).format(date); //  'en-US' 
        })
        .style("font-size", "15px")
        .style("font-weight", "bold") // 
        .style("fill", "#000000");// 

      // 3. 
      svg.append("text")
        .attr("class", "axis-label")
        .attr("x", (timelineWidth *0.95))  // 
        .attr("y", centerY + timelineHeight / 2 - 10) // 
        .attr("text-anchor", "middle")
        .text("")  // 
        .style("font-size", "16px")
        .style("font-weight", "bold");// ""

      // 4. 
      const lineGroup = svg.append("g").attr("class", "timeline-link");


      // 
      lineGroup.selectAll("path")  // pathline
          .data(this.selectedTimeData)
          .enter()
          .append("path")
          .attr("class", "timeline-link")
          .attr("d", (d, i) => {
            // 
            const offsetX = i * 0;  //  5px
            const offsetY = 0;  // yy

            // 
            const start = [xScale(new Date(d.time)) + offsetX, centerY + timelineHeight / 2 + offsetY];  // xy
            

            // xy
            const end = this.projection([d.lon, d.lat]);
            

            //  ()
            const controlPoint = [
                (start[0] + end[0]) / 2, // x
                (start[1] + end[1]) / 2 - 50  // y
            ];

              // 
            return d3.line().curve(d3.curveCardinal).x(d => d[0]).y(d => d[1])([start, controlPoint, end]);
              // 
          })
          .attr("fill", "none")
          .attr("stroke", "#f19c4c")// (v1:b7ae8f)
          .attr("stroke-width", 2)
          .style("opacity", 0.5)
          .style("stroke-dasharray", "4,4");  // 
      // 
      const rectWidth = 35;  // 
      const rectHeight = 90; // 
      const offsets = [-73, 98, 12]; // 
      lineGroup.selectAll(".timeline-rect")
        .data(this.selectedTimeData)
        .enter()
        .append("rect")
        .attr("class", "timeline-rect")
        .attr("x", (d, i) => {
          const baseX = xScale(new Date(d.time)); // 
          const offsetX = i * 0;  // 
          return baseX + offsetX - rectWidth / 2; // 
        })
        .attr("y", (d, i) => {
          const baseY = centerY + 130; // 
          const offsetY = offsets[i % 3];  // 
          return baseY + offsetY;  // 
        })
        .attr("width", rectWidth)
        .attr("height", rectHeight)
        .attr("rx", 8)  // 
        .attr("ry", 8)
        .attr("fill", "#b7ae8f")// 
        .attr("style", "pointer-events: visible")
        .on("click", (event, d) => {  // 
          console.log(this);  //  this  rect 
          const placeImpo = d.impo;
          const placeInfo = d.info;
          this.placeImpoDisplay = placeImpo ? placeImpo : "";
          this.placeInfoDisplay = placeInfo ? placeInfo : "";
            // 
          // d3.select(event.target).attr("fill", "#f9f9f9");
        })
        .on("mouseover", function() {
          d3.select(this)  // 
            .transition()  // 
            .duration(300)  // 300ms
            .attr("fill", "#f9f9f9")  // 
            .attr("cursor", "pointer")  // 
            .style("opacity", 0.8);  // 
        })
        .on("mouseout", function() {
          d3.select(this)  // 
            .transition()  // 
            .duration(300)  // 300ms
            .attr("fill", "#b7ae8f")  // 
            .style("opacity", 1);  // 
        });


      // 
      lineGroup.selectAll(".timeline-line")
        .data(this.selectedTimeData)
        .enter()
        .append("line")
        .attr("class", "timeline-line")
        .attr("x1", (d, i) => {
          const baseX = xScale(new Date(d.time)); // x
          const offsetX = i * 0;  // 
          return baseX + offsetX;
        })
        .attr("y1", centerY + timelineHeight / 2) // y
        .attr("x2", (d, i) => {
          const baseX = xScale(new Date(d.time)); // x
          const offsetX = i * 0;  // 
          return baseX + offsetX;
        })
        .attr("y2", (d, i) => {
          const baseY = centerY+ 130; // 
          const offsetY = offsets[i % 3];  // 
          return baseY + offsetY;  // 
        })
        .attr("stroke", "#b7ae8f") // 
        .attr("stroke-width", 2)
        .style("opacity", 0.5);
      // const rectWidth = 30;  // 
      // const rectHeight = 80; // 
      // 
      lineGroup.selectAll(".timeline-text")
        .data(this.selectedTimeData)
        .enter()
        .append("text")
        .attr("class", "timeline-text")
        .attr("x", (d, i) => {
          const baseX = xScale(new Date(d.time)); // 
          const offsetX = i * 0;  // 
          return baseX + offsetX;
        })
        .attr("y", (d, i) => {
          const baseY = centerY + 128; // 
          const offsetY = offsets[i % 3];  // 
          return baseY + offsetY;  // 
        })
        .attr("text-anchor", "middle")
        .attr("font-size", "12px")
        .attr("fill", "#000")//
        .attr("style", "pointer-events: none")
        .each(function(d) {
          const textElement = d3.select(this);
          const characters = d.name.split(""); //  name 
          const num = 6; // 
          const columnOffset = 12; // 
          const set2 = -2; //  2  10
          const set3 = 1.5; //  3  15
          const set4 = 5; //  3  15
          const set5 = 8;  //  5  20

          //  x 
          const setX2 = 6; //  2 x 
          const setX3 = 12; //  3 x 
          const setX4 = 11; //  3 x 

          //  y 
          const totalCharacters = characters.length;
          let yOffset = 0; //  0
          if (totalCharacters === 2) {
            yOffset = set2; //  2  y 
          } else if (totalCharacters === 3) {
            yOffset = set3; //  3  y 
          } else if (totalCharacters === 4) {
            yOffset = set4; //  3  y 
          } else if (totalCharacters >= 5) {
            yOffset = set5; //  5  y 
          }
          textElement.attr("y", +textElement.attr("y") + yOffset); //  y 
          // 
          const lineHeight = totalCharacters <= num ? 6 / totalCharacters : 1;

          //  x 
          const totalColumns = Math.ceil(totalCharacters / num); // 
          let xOffset = 0; //  x  0
          if (totalColumns === 2) {
            xOffset = setX2; //  2  x 
          } else if (totalColumns === 3) {
            xOffset = setX3; //  3  x 
          }else if (totalColumns === 4) {
            xOffset = setX4; //  3  x 
          }
          textElement.attr("x", +textElement.attr("x") + xOffset); //  x 

          characters.forEach((char, index) => {
            const column = Math.floor(index / num); // 
            const row = index % num; // 

            textElement.append("tspan")
              .attr("x", +textElement.attr("x") - column * columnOffset) // 
              .attr("dy", row === 0 && column > 0 ? `-${(num - 1) * lineHeight}em` : lineHeight + "em")
              .text(char); //  tspan 
          });
        });

      console.log("Relating data completed");
    },

    // 
    async drawLocationCircles(svg, projection, locations) {
      return new Promise((resolve) => {
        svg.append("g")
          .selectAll("circle")
          .data(this.locations)
          .enter()
          .append("circle")
          .attr("cx", (d) => projection([d.lon, d.lat])[0]) // 
          .attr("cy", (d) => projection([d.lon, d.lat])[1])
          .attr("r", 2) // 
          .attr("fill", d => 
            this.init === 0 
              ? "#8f5442" 
              : (locations.includes(d) ? "#f19c4c" : "#8f5442")
          )
          .on("click", (d) => {
            console.log(`Clicked on location: ${d.name}`);
          });

        //  resolve
        resolve();
      });
    },

    // 
    async drawLocationLines(svg, projection, locations, len) {
      return new Promise((resolve) => {
        const lineGroup = svg.append("g");
        for (let i = 0; i < this.locations.length - 1; i++) {
          const start = this.locations[i];
          const end = this.locations[i + 1];
          //  len
          if (Math.abs(start.lon - end.lon) < len && Math.abs(start.lat - end.lat) < len) {
            lineGroup.append("line")
              .attr("class", "location-line") //  class
              .attr("x1", projection([start.lon, start.lat])[0])
              .attr("y1", projection([start.lon, start.lat])[1])
              .attr("x2", projection([end.lon, end.lat])[0])
              .attr("y2", projection([end.lon, end.lat])[1])
              .attr("stroke", () => {
                if (this.init === 0) {
                  return "#8f5442"; //  this.init  0 
                } else {
                  // 
                  return locations.includes(start) && locations.includes(end) ? "#f19c4c" : "#8f5442";
                }
              }) //
              .attr("stroke-width", 1); // 
          }
        }

        //  resolve
        resolve();
      });
    },
    async initMap() {
      try {
        // // SVG
        // const images = container.querySelectorAll('img');
        // images.forEach(img => img.remove());  // 

        // const svgs = container.querySelectorAll('svg');
        // svgs.forEach(svg => svg.remove());  //  SVG 
        //  `mapContainer`  DOM 
        const container = this.$refs.mapContainer;
        console.log("Container height:", container.clientHeight);
        // 
        this.projection = d3.geoMercator()
          .center([107, 20]) // 
          .scale(container.clientHeight * 0.8) // 
          .translate([container.clientWidth / 1.85, container.clientHeight / 1.62]);

        //  SVG CSS 
        this.svg = d3
          .select(container)
          .append("svg")
          .attr("preserveAspectRatio", "xMidYMid meet") // 
          .attr("viewBox", `0 0 ${container.clientWidth} ${container.clientHeight}`);

        // 
        await this.drawMapPath(this.svg, this.projection);
        await this.drawLocationCircles(this.svg, this.projection, this.locations);
        await this.drawLocationLines(this.svg, this.projection, this.locations, this.len);

        // 
        this.initTimeline(this.svg, container.clientHeight / 1.6); // 
      } catch (error) {
        console.error(":", error);
      }
    },
    initTimeline(mapSvg, mapHeight) {
      //  mapSvg 
      if (!mapSvg || !mapSvg.node()) {
        console.error('SVG is not initialized correctly.');
        return;
      }
      const svg = mapSvg.append("g").attr("class", "timeline-group");

      const timelineWidth = mapSvg.node().getBoundingClientRect().width;
      const timelineHeight = 60;  //  60px

      //  y 
      const timelineYPosition = mapHeight + 20; // 
      this.timelineY = timelineYPosition; //  y 
      this.timelineX = timelineWidth;
      // 
      const xScale = d3.scaleTime()
        .domain([d3.min(this.timeData, d => new Date(d.time)),
          d3.max(this.timeData, d => new Date(d.time))])
        .range([0, timelineWidth]);

      // 
      const line = d3.line()
        .x(d => xScale(new Date(d.time)))  // 
        .y(timelineHeight / 2)  // 
        .curve(d3.curveMonotoneX);  // 
      svg.append("path")
        .data([this.timeData]) // 
        .attr("d", line)
        .attr("fill", "none")
        .attr("stroke", "#b7ae8f")//
        .attr("stroke-width", 3)
        .attr("marker-end", "url(#arrow)")  // 
        .attr("transform", `translate(0, ${timelineYPosition})`); // 

      console.log(xScale(new Date(this.timeData[50].time))); // X

      // 
      const ticks = xScale.ticks(d3.timeYear.every(1)); // 
      svg.selectAll(".tick")
        .data(ticks)
        .enter().append("line")
        .attr("class", "tick")
        .attr("x1", d => xScale(d))
        .attr("x2", d => xScale(d))
        .attr("y1", 0)
        .attr("y2", timelineHeight)
        .attr("stroke", "#b7ae8f")// 
        .attr("stroke-width", 1)
        .attr("transform", `translate(0, ${timelineYPosition})`); // 

      // marker
      svg.append("defs").append("marker")
        .attr("id", "arrow")  // ID
        .attr("viewBox", "0 0 10 10")  // 
        .attr("refX", 8)  // 
        .attr("refY", 5)  // Y
        .attr("markerWidth", 4)  // 
        .attr("markerHeight", 4)  // 
        .attr("orient", "auto")  // 
        .attr("transform", `translate(0, ${timelineYPosition})`) // 
        .append("path")
        .attr("d", "M 0 0 L 10 5 L 0 10 z")  // 
        .attr("fill", "#b7ae8f");  // 
        
    },    

    
    createTooltip(svg, d, event) {
      const { x, y } = this.mouseXY(svg, event);

      svg
        .append("rect")
        .attr("id", "tooltip-box")
        .attr("x", x)
        .attr("y", y)
        .attr("width", 100)
        .attr("height", 40)
        .attr("fill", "#000")//
        .attr("opacity", 0.6);

      svg
        .append("text")
        .attr("id", "tooltip-text")
        .attr("x", x + 50)
        .attr("y", y + 25)
        .attr("text-anchor", "middle")
        .attr("font-size", "14px")
        .attr("fill", "#fff")//
        .text(d ? d.properties.name : "");
    },
    removeTooltip() {
      d3.select("#tooltip-box").remove();
      d3.select("#tooltip-text").remove();
    },
    mouseXY(svg, event) {
      const rect = svg.node().getBoundingClientRect();
      return { x: event.clientX - rect.left, y: event.clientY - rect.top };
    },
  },
};
</script>

